.person {
  border: 1px lightgray solid;
  padding: 30px;
  margin: 10px;
  background-color: white;
}

.person:hover {
  box-shadow: 0px 10px 15px 10px gray;
  transition: 0.3s;
}

.person--image:hover {
  transform: scale(1.05);
  transition: 0.3s;
}

.person--image {
  border-radius: 100%;
  border: 1px solid;
  height: 200px;
  width: 200px;
  cursor: pointer;
}

.person--image:hover ~ h1 {
  transition: 0.3s;
  color: orange;
}
