#bernardFamily img {
  height: 500px;
  width: 800px;
  border: 5px solid;
  border-radius: 5px;
}

#bernardFamily h1 {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-weight: 1000;
  background-color: white;
  border: 1px solid;
  border-radius: 15px;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
}

#bernardFamily p {
  background-color: white;
  border-radius: 10px;
  padding: 20px;
  border: 1px solid;
  font-size: 25px;
  width: 1000px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
}

.faIcon {
  background-color: white;
  color: rgb(41, 142, 224);
  display: flex;
  margin-left: auto;
  padding: 15px;
  border-radius: 100%;
}

.faIcon:hover {
  background-color: rgb(41, 142, 224);
  color: aliceblue;
  cursor: pointer;
}
