@media screen and (min-width: 950px) {
  .personShow {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 70px;
    width: 1500px;
    margin-left: auto;
    margin-right: auto;
  }
}

body {
  background-image: url("https://wallpapercave.com/wp/EO4r03r.jpg");
}

.personShow--header {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  background-color: #273442;
  color: aliceblue;
  margin-top: 0;
  padding: 10px;
  border-bottom: black 1px solid;
  position: fixed;
  top: 0;
  transition: 0.2s;
  width: 100%;
}
